.nav-toggle {
   display: block;
   border-radius: 5px;
   background-color: transparent;
   float: right;
   height: 38px;
   width: 38px;
   cursor: pointer;
   padding: 8px 8px;
   margin-top: 10px;
}
.nav-toggle.open span:first-child {
   transform: rotate(45deg) translate(4.4px, 4.4px);
}
.nav-toggle.open span:nth-child(2) {
   width: 0%;
   opacity: 0;
}
.nav-toggle.open span:last-child {
   transform: rotate(-45deg) translate(4.4px, -4.4px);
}
@media (min-width: 768px) {
   .nav-toggle {
      display: none;
   }
}
